import { Injectable } from '@angular/core';
import { Observable, ReplaySubject, Subject } from 'rxjs';

@Injectable()
export class ActionsProductNotificationsSettingsRouterService {
  private readonly productNotificationsSettingsSavedSubject: Subject<void> = new Subject();

  /**
   * Stream of settings save events that trigger navigation.
   */
  readonly settingsSavedNavigation$: Observable<void> = this.productNotificationsSettingsSavedSubject.asObservable();

  private readonly productNotificationsSettingsCancelledSubject: Subject<void> = new Subject();

  /**
   * Stream of settings cancel events that trigger navigation.
   */
  readonly settingsCancelledNavigation$: Observable<void> =
    this.productNotificationsSettingsCancelledSubject.asObservable();

  /**
   * Selected account ID setter.
   */
  set selectedAccountId(value: string) {
    this.selectedAccountIdSubject.next(value);
  }
  private readonly selectedAccountIdSubject: ReplaySubject<string> = new ReplaySubject(1);

  /**
   * Stream with selected account IDs.
   */
  readonly selectedAccountId$: Observable<string> = this.selectedAccountIdSubject.asObservable();

  /**
   * Method to emit settings saved event in a stream.
   */
  emitSettingsSavedNavigation() {
    this.productNotificationsSettingsSavedSubject.next();
  }

  /**
   * Method to emit settings cancelled event in a stream.
   */
  emitSettingsCancelledNavigation() {
    this.productNotificationsSettingsCancelledSubject.next();
  }
}
