import { Component, Directive, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import {
  ActionRecipeFormItemValue,
  ActionRecipeItem,
  getPropertyValue,
  Notification,
  SpecificationIds,
  SpecificationsWithAction,
} from '@backbase/actions-common-ang';
import { ActionRecipeGetResponseBody, ActionRecipeSpecification } from '@backbase/data-ang/actions';
import { ActionsStoreModel } from '@backbase/actions-store-ang';
import { AccountArrangementItem } from '@backbase/data-ang/arrangements';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import {
  catchError,
  first,
  map,
  mapTo,
  publishReplay,
  refCount,
  startWith,
  switchMap,
  withLatestFrom,
} from 'rxjs/operators';
import { ActionsProductNotificationsSettingsDataService } from '../../../services/actions-product-notifications-settings-data.service';
import { ActionsProductNotificationsSettingsPropertiesService } from '../../../services/actions-product-notifications-settings-properties.service';
import { ActionsProductNotificationsSettingsRouterService } from '../../../services/actions-product-notifications-settings-router.service';

@Component({
  selector: 'bb-actions-product-settings-page',
  templateUrl: './actions-product-settings-page.component.html',
})
export class ActionsProductSettingsPageComponent implements OnInit {
  private readonly notificationDismissTime$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly specificationIDs$$ = new BehaviorSubject<string[] | undefined>(undefined);

  /**
   * Notification dismiss time value setter.
   * Configurable via model property.
   */
  @Input() set notificationDismissTime(value: number | undefined) {
    this.notificationDismissTime$$.next(value);
  }

  /**
   * Specification IDs value setter.
   * Configurable via model property.
   */
  @Input() set specificationIDs(value: string[] | undefined) {
    this.specificationIDs$$.next(value);
  }

  /**
   * Stream of account arrangement item data.
   */
  readonly account$: Observable<AccountArrangementItem>;

  private readonly accountId$: Observable<string>;

  /**
   * Stream of Specifications with action recipe.
   */
  readonly specificationsWithActionRecipe$?: Observable<SpecificationsWithAction> =
    this.store.specificationsWithActionRecipe$;

  /**
   * Stream of loading flag indicators.
   */
  readonly isLoading$: Observable<boolean>;

  private readonly hasAccountLoadingError = new BehaviorSubject(false);

  /**
   * Stream of loading errors.
   */
  readonly loadingError$ = combineLatest([
    this.store.actionRecipesError$,
    this.store.specificationsError$,
    this.hasAccountLoadingError,
  ]).pipe(map(errors => errors.find(Boolean)));

  /**
   * Specification IDs enum.
   */
  readonly specificationIds = SpecificationIds;

  /**
   * Error notification header template.
   */
  @ViewChild('errorNotificationHeader', { static: false }) errorNotificationHeaderTemplate!: TemplateRef<any>;

  /**
   * Error notification message template.
   */
  @ViewChild('errorNotificationMessage', { static: false }) errorNotificationMessageTemplate!: TemplateRef<any>;

  constructor(
    private readonly store: ActionsStoreModel,
    private readonly routerService: ActionsProductNotificationsSettingsRouterService,
    private readonly widgetDataService: ActionsProductNotificationsSettingsDataService,
    private readonly widgetPropertiesService: ActionsProductNotificationsSettingsPropertiesService,
  ) {
    this.accountId$ = this.routerService.selectedAccountId$;
    this.account$ = this.accountId$.pipe(
      switchMap(id => this.getAccount(id)),
      publishReplay(),
      refCount(),
    );

    this.isLoading$ = combineLatest([
      this.store.actionRecipesLoading$,
      this.store.specificationsLoading$,
      this.account$.pipe(mapTo(false), startWith(true)),
    ]).pipe(map(loadings => loadings.some(Boolean)));
  }

  ngOnInit() {
    const specificationIdsStream = getPropertyValue(
      this.specificationIDs$$,
      this.widgetPropertiesService.getSpecificationIDs(),
    );

    this.accountId$.pipe(first(), withLatestFrom(specificationIdsStream)).subscribe(([accountId, specificationIDs]) => {
      const specificationIDsSet = new Set(specificationIDs);
      this.store.dispatchLoadSpecifications({ specificationIds: specificationIDsSet });
      this.store.dispatchLoadActionRecipes({
        specificationIds: specificationIDsSet,
        arrangementId: accountId,
      });
    });
  }

  /**
   * Method to submit action recipe form.
   *
   * @param formValue - action recipe form value
   */
  onSubmit(formValue: ActionRecipeFormItemValue) {
    const notificationDismissTime$ = getPropertyValue(
      this.notificationDismissTime$$,
      this.widgetPropertiesService.getNotificationDismissTimeProperty(),
    );

    combineLatest([this.accountId$, notificationDismissTime$])
      .pipe(first())
      .subscribe(([arrangementId, ttl]) => {
        const errorNotification: Notification = {
          header: Object.create(this.errorNotificationHeaderTemplate),
          message: Object.create(this.errorNotificationMessageTemplate),
          modifier: 'error',
          ttl,
        };

        this.store.dispatchSaveActionRecipe({
          actionRecipe: formValue,
          arrangementId,
          warningNotification: errorNotification,
        });
      });
  }

  private getAccount(id: string): Observable<AccountArrangementItem> {
    return this.widgetDataService.getAccountById(id).pipe(
      catchError(() => {
        this.hasAccountLoadingError.next(true);

        return of({} as AccountArrangementItem);
      }),
    );
  }
}

@Directive({
  selector: '[bbActionsProductSettingsPageCustomizable]',
})
export class ActionsProductSettingsPageCustomizableDirective extends BbTemplate<
  ActionsProductSettingsPageComponent,
  {
    actions: (ActionRecipeGetResponseBody | ActionRecipeItem)[];
    specifications: ActionRecipeSpecification[];
    account: AccountArrangementItem;
    specificationsWithAction: SpecificationsWithAction;
    specificationIds: SpecificationIds;
  }
> {}

@Directive({
  selector: '[bbActionsProductSettingsPageErrorNotificationHeaderCustomizable]',
})
export class ActionsProductSettingsPageErrorNotificationHeaderCustomizableDirective extends BbTemplate<
  ActionsProductSettingsPageComponent,
  void
> {}

@Directive({
  selector: '[bbActionsProductSettingsPageErrorNotificationMessageCustomizable]',
})
export class ActionsProductSettingsPageErrorNotificationMessageCustomizableDirective extends BbTemplate<
  ActionsProductSettingsPageComponent,
  void
> {}
